<template>
  <v-container ref="boxIds" class="regression_container">
    <template v-if="isSkeleton">
      <space-ui style="width: 100%" sign="regression" />
    </template>
    <template v-else>
      <div class="page regression">
        <div class="regression_title">
          <div class="lt">{{ $t('UI_Regression_Finisn_Text') }}</div>
          <div class="rt">
            <template v-if="isDayShow">
              <div class="rt_day">
                <div class="text">{{ $t('UI_Regression_Remaining_Text') }}</div>
                <div class="num">{{ activityTime.day }}</div>
                <div class="text">
                  {{ Number(activityTime.day) > 1 ? $t('UI_Regression_Days_Text') : $t('UI_Regression_Day_Text') }}
                </div>
              </div>
            </template>
            <template v-else>
              <div class="rt_hour">
                <div class="num">{{ activityTime.hh }}</div>
                <div class="text">{{ $t('UI_Regression_Hours_Text') }}</div>
              </div>
              <div class="colon">&nbsp;:&nbsp;</div>
              <div class="rt_min">
                <div class="num">{{ activityTime.mm }}</div>
                <div class="text">{{ $t('UI_Regression_Min_Text') }}</div>
              </div>
              <div class="colon">&nbsp;:&nbsp;</div>
              <div class="rt_sec">
                <div class="num">{{ activityTime.ss }}</div>
                <div class="text">{{ $t('UI_Regression_Sec_Text') }}</div>
              </div>
            </template>
          </div>
        </div>
        <template v-if="Object.keys(activityB).length > 0">
          <div class="regression_task">
            <div class="task_explain_text">{{ activityB.content || '' }}</div>
            <template v-if="activityBList.length > 0">
              <div class="regression_task_list">
                <div
                  class="task_item"
                  v-for="(item, i) in activityBList"
                  :key="i"
                  @click="
                    onActivityReceive({
                      day_index: i + 1,
                      action_type: activityB.action_type,
                      inside_data: activityB.inside_data,
                      back_task_join_id: activityB.back_task_join_id,
                      ...item
                    })
                  "
                >
                  <div class="task_item_img">
                    <img :src="loadIcon('regression_box')" alt="" />
                    <!-- 光效 -->
                    <template v-if="item.is_finish == 1">
                      <img class="task_item_light_img" :src="loadIcon('regression_box_light')" alt="" />
                    </template>
                    <template v-if="i == activityBList.length - 1">
                      <div class="task_item_bg_one"></div>
                      <div class="task_item_bg_two"></div>
                      <div class="task_item_bg_three"></div>
                    </template>
                    <!-- 未达标 -->
                    <template v-if="item.is_finish == 0">
                      <div class="task_item_not">
                        <icon-font
                          name="icon-Icon_Lock"
                          :size="ommonStyle(isApp ? 'regression_item_icon_app_size' : 'regression_item_icon_pc_size')"
                          :dark="ommonStyle(isApp ? 'regression_item_icon_app_color' : 'regression_item_icon_pc_color')"
                        />
                      </div>
                    </template>
                  </div>
                  <div class="task_item_money">
                    <img class="task_item_money_chip" :src="loadIcon('regression_chip')" alt="" />
                    <span class="task_item_money_text">&nbsp;{{ currencySymbol }}{{ item.gold || 0 }}</span>
                  </div>

                  <!-- 已领取 -->
                  <template v-if="item.is_finish == 2">
                    <div class="task_item_make"></div>
                    <img class="task_item_success" :src="loadIcon('regression_box_success')" alt="" />
                  </template>
                </div>
              </div>
            </template>
          </div>
        </template>
        <div class="activity_list">
          <div class="activity_item" v-for="item in activityList" :key="item.action_type">
            <div class="activity_item_title">{{ item.content || '' }}</div>
            <div class="activity_item_line">
              <div class="activity_item_line_lt">
                <span class="lt_text">{{ item.cur_val }}/{{ item.target_val }}</span>
                <div class="lt_speed">
                  <div
                    class="lt_speed_slider"
                    :style="`
											width: ${((item.cur_val || 0) / (item.target_val || 0) > 1 ? 1 : (item.cur_val || 0) / (item.target_val || 0)) * 100}%;
										`"
                  ></div>
                </div>
              </div>
              <div class="activity_item_line_rt">
                <!-- is_finish 是否完成，0未完成，1完成，2完成并领取 -->
                <div
                  class="regression_complete_btn"
                  :class="{
                    complete: item.is_finish == 1,
                    disabled: item.is_finish == 0,
                    received: item.is_finish == 2
                  }"
                  v-zoom
                  @click="onActivityReceive(item)"
                >
                  <auto-font
                    uuid="regressionBtnId"
                    :maxFont="
                      ommonStyle(isApp ? 'regression_item_line_rt_btn_app_size' : 'regression_item_line_rt_btn_pc_size')
                    "
                    :level="2"
                    :text="item.is_finish == 0 ? $t('UI_Regression_Go_Text') : $t('UI_Regression_Get_Text')"
                  />
                </div>
                <img
                  v-if="item.is_finish == 2"
                  class="regression_complete_icon"
                  :src="loadIcon('regression_activity_success')"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import PublishSubscribe from 'publish-subscribe-js'
import { regressionType } from '@/events/game.js'
import { jump } from '@/utils/linkMap.js'
import { getRegressionList, getRegressionReceiveGold } from '@/api/modules/regression.js'
import _common from '@style/views/regression.modules.scss'
import spaceUi from '@/components/skeleton/space-ui.vue'

export default {
  name: 'regression',
  components: {
    spaceUi
  },
  data() {
    return {
      isSkeleton: true, // 骨架屏，只有初始化进来的时候显示
      // 活动时间
      activityTime: {
        day: '00',
        hh: '00',
        mm: '00',
        ss: '00'
      },
      isRegressionEnd: false, // 活动是否结束
      activityA: {}, // 活动A
      activityB: {}, // 活动B
      activityC: {}, // 活动C
      activityD: {} // 活动D
    }
  },
  computed: {
    ...mapState('base', ['user', 'isApp', 'languageType', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 是否只显示天数
    isDayShow() {
      const { day = 0, hh = 0, mm = 0, ss = 0 } = this.activityTime

      return Number(day) >= 1 && Number(hh) >= 0 && Number(mm) >= 0 && Number(ss) >= 0
    },
    /**
     * 任务B参与列表
     * complete_time		完成时间
     * gold					奖励金额
     * is_finish 			是否完成，0未完成，1完成，2完成并领取
     * receive_time			领取时间
     */
    activityBList() {
      const { action_detail = [] } = this.activityB

      return action_detail
    },
    // 任务列表
    activityList() {
      let list = []
      if (Object.keys(this.activityA).length > 0) list.push(this.activityA)
      if (Object.keys(this.activityC).length > 0) list.push(this.activityC)
      if (Object.keys(this.activityD).length > 0) list.push(this.activityD)

      const firstList = list.filter(f => f.is_finish == 1)
      const centerList = list.filter(f => f.is_finish == 0)
      const lastList = list.filter(f => f.is_finish == 2)

      return [...firstList, ...centerList, ...lastList]
    }
  },
  created() {
    this.getList()
    // 接收ws通知，更新
    PublishSubscribe.subscribe(regressionType.REGRESSION_CONFIG, this.updateData)
  },
  methods: {
    loadIcon: k => loadIcon(k, false),
    ommonStyle: k => _common[k],
    // 获取列表
    async getList() {
      const params = {
        token: this.user?.token,
        language: this.languageType
      }
      // this.isSkeleton = true;
      const res = await getRegressionList(params).catch(e => e)
      this.isSkeleton = false
      if (!res || res.code != 0) return this.$pop.message().show({ content: res && res.msg })

      const resList = res.data && Array.isArray(res.data) ? res.data : []
      resList.forEach(item => {
        // 活动时间：只有在活动A里返回。以防万一活动A关闭了，在其他的活动里返回
        if (item.rest_time && item.rest_time >= 0) {
          const num = item.rest_time || 0
          const end_time = Math.round(Date.now() / 1000) + num
          this.activityTime = this.$u.datetime.getDifferTime(new Date(), new Date(end_time * 1000))
          this.countDown(end_time)
        }

        if (item.action_type == 1) this.activityA = item
        if (item.action_type == 2) this.activityB = item
        if (item.action_type == 3) this.activityC = item
        if (item.action_type == 4) this.activityD = item
      })
    },
    /**
     * 活动倒计时
     * @param {Number} end_time 结束时间
     */
    countDown(endTime = 0) {
      const curTime = Math.round(Date.now() / 1000)
      this.isRegressionEnd = curTime > Number(endTime)

      if (this.countDownTimer) {
        clearTimeout(this.countDownTimer)
        this.countDownTimer = null
      }
      if (!this.isRegressionEnd) this.activityTime = this.$u.datetime.getDifferTime(curTime * 1000, endTime * 1000)
      this.countDownTimer = setTimeout(() => {
        if (this.isRegressionEnd) return
        this.countDown(endTime)
      }, 1 * 1000)
    },
    /**
     * 活动B领取奖励
     * action_type			活动类型 1活动A 2活动B 3活动C 4活动D
     * day_index 			七日签到的序号，1，2，3，4，5，6，7
     * is_finish 			是否完成，0未完成，1完成，2完成并领取
     * back_task_join_id 	任务ID
     * inside_data			内链数据
     */
    async onActivityReceive(item = {}) {
      const { action_type = 1, day_index = 1, is_finish = 0, back_task_join_id = 0, inside_data = {} } = item
      if (is_finish != 1) {
        if (Object.keys(inside_data).length > 0 && is_finish != 2) jump({ inside_data }, this)
        return
      }

      let params = {
        token: this.user?.token,
        language: this.languageType,
        back_task_join_id
      }
      if (action_type == 2) params.day_index = day_index

      const res = await getRegressionReceiveGold(params).catch(e => e)
      if (!res || res.code != 0) return this.$pop.message().show({ content: res && res.msg })

      const resData = res.data && Object.prototype.toString.call(res.data) == '[object Object]' ? res.data : {}
      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Code_Bonus'),
          value: resData.gold || 0
        }
      })

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'back_task',
        token: this.user?.token,
        language: this.languageType
      })

      this.getList()
    },
    // 更新数据
    updateData() {
      this.getList()
      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'back_task',
        token: this.user?.token,
        language: this.languageType
      })
    }
  },
  beforeDestroy() {
    if (this.countDownTimer) {
      clearTimeout(this.countDownTimer)
      this.countDownTimer = null
    }
    // 卸载订阅
    PublishSubscribe.unsubscribe(regressionType.REGRESSION_CONFIG)
  }
}
</script>

