              border-radius: $regression_activity_item_line_rt_btn_app_radius;
              font-weight: $regression_activity_item_line_rt_btn_app_weight;
              color: $regression_activity_item_line_rt_btn_app_color;
              line-height: $regression_activity_item_line_rt_btn_app_line;
              cursor: pointer;
              @include setBox(row, center, center);
              &.complete {
                background: $regression_activity_item_line_rt_btn_app_bg;
              }
              &.disabled {
                background: $regression_activity_item_line_rt_btn_disabled_app_bg;
              }
              &.received {
                background: $regression_activity_item_line_rt_btn_received_app_bg;
              }
            }
            .regression_complete_icon {
              width: $regression_activity_item_line_rt_icon_app_width;
              position: absolute;
              top: $regression_activity_item_line_rt_icon_app_top;
              left: $regression_activity_item_line_rt_icon_app_left;
              transform: $regression_activity_item_line_rt_icon_app_transform;
            }
          }
        }
      }
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .regression_container {
    .page.regression {
      width: $regression_page_pc_width;
      padding: $regression_page_pc_padding;
      .regression_title {
        margin: $regression_title_pc_margin;
        .lt {
          font-size: $regression_title_lt_pc_size;
        }
        .rt {
          .rt_day {
            .text {
              font-size: $regression_title_rt_day_text_pc_size;
            }
            .num {
              width: $regression_title_rt_day_num_pc_width;
              height: $regression_title_rt_day_num_pc_height;
              border-radius: $regression_title_rt_day_num_pc_radius;
              font-size: $regression_title_rt_day_num_pc_size;
            }
          }
          .rt_hour,
          .rt_min,
          .rt_sec {
            .num {
              width: $regression_title_rt_time_num_pc_width;
              height: $regression_title_rt_time_num_pc_height;
              border-radius: $regression_title_rt_time_num_pc_radius;
              font-size: $regression_title_rt_time_num_pc_size;
            }
            .text {
              font-size: $regression_title_rt_time_text_pc_size;
            }
          }
          .colon {
            font-size: $regression_title_rt_colon_pc_size;
          }
        }
      }
      .regression_task {
        padding: $regression_task_pc_padding;
        margin: $regression_task_pc_margin;
        border-radius: $regression_task_pc_radius;
        .task_explain_text {
          font-size: $regression_task_explain_text_pc_size;
          line-height: $regression_task_explain_text_pc_line;
        }
        .regression_task_list {
          margin: $regression_task_list_pc_margin;
          .task_item {
            width: $regression_task_item_pc_width;
            margin: $regression_task_item_pc_margin;
            padding: $regression_task_item_pc_padding;
            border-radius: $regression_task_item_pc_radius;
            .task_item_img {
              width: $regression_task_item_img_pc_width;
              min-height: $regression_task_item_img_pc_min_height;
              .task_item_bg_one {
                width: $regression_task_item_bg_one_pc_width;
                height: $regression_task_item_bg_one_pc_height;
              }
              .task_item_bg_two {
                width: $regression_task_item_bg_two_pc_width;
                height: $regression_task_item_bg_two_pc_height;
              }
              .task_item_bg_three {
                width: $regression_task_item_bg_three_pc_width;
                height: $regression_task_item_bg_three_pc_height;
              }
              .task_item_not {
                width: $regression_task_item_bg_not_pc_width;
                height: $regression_task_item_bg_not_pc_height;
              }
            }
            .task_item_money {
              margin: $regression_task_item_money_pc_margin;
              .task_item_money_chip {
                width: $regression_task_item_money_chip_pc_width;
              }
              .task_item_money_text {
                font-size: $regression_task_item_money_text_pc_size;
              }
            }
            .task_item_success {
              width: $regression_task_item_success_pc_width;
            }
            &:last-child {
              width: $regression_task_item_last_pc_width;
            }
          }
        }
      }
      .activity_list {
        .activity_item {
          margin: $regression_activity_item_pc_margin;
          padding: $regression_activity_item_pc_padding;
          border-radius: $regression_activity_item_pc_radius;
          .activity_item_title {
            font-size: $regression_activity_item_title_pc_size;
          }
          .activity_item_line {
            margin: $regression_activity_item_line_pc_margin;
            .activity_item_line_lt {
              .lt_text {
                font-size: $regression_activity_item_line_lt_text_pc_size;
              }
              .lt_speed {
                height: $regression_activity_item_line_lt_speed_pc_height;
                margin: $regression_activity_item_line_lt_speed_pc_margin;
                border-radius: $regression_activity_item_line_lt_speed_pc_radius;
                .lt_speed_slider {
                  border-radius: $regression_activity_item_line_lt_speed_slider_pc_radius;
                }
              }
            }
            .activity_item_line_rt {
              margin: $regression_activity_item_line_rt_pc_margin;
              .regression_complete_btn {
                width: $regression_activity_item_line_rt_btn_pc_width;
                height: $regression_activity_item_line_rt_btn_pc_height;
                padding: $regression_activity_item_line_rt_btn_pc_padding;
                border-radius: $regression_activity_item_line_rt_btn_pc_radius;
              }
              .regression_complete_icon {
                width: $regression_activity_item_line_rt_icon_pc_width;
              }
            }
          }
        }
      }
    }
  }
}
</style>
